import React, { Component } from 'react'
import {Link} from 'react-router-dom'
import { Layout, Menu, Breadcrumb, Icon,Row, Col,Form,Input,Button,Upload  } from 'antd';
import { withRouter } from 'react-router-dom'
import {logout} from '../../../action/login'
import {connect} from 'react-redux'
import './edit.css'

const { Header, Content, Sider } = Layout;

class Edit extends Component {

  constructor(){
    super()
    this.state={
      name:null,
      email:null,
      gender:null
    }
    this.handleLogout=this.handleLogout.bind(this)
    this.onChange=this.onChange.bind(this)
    this.emitEmailEmpty=this.emitEmailEmpty.bind(this)
    this.emitNameEmpty=this.emitNameEmpty.bind(this)
  }

  componentDidMount() {
    // console.log()
    if(this.props.location.state!=null){
      if(this.props.location.state.item!=null){
        this.setState({
          name:this.props.location.state.item.name.last,
          email:this.props.location.state.item.email,
          gender:this.props.location.state.item.gender
        })
      }
    }

  }



  onChange=(e)=>{
    this.setState({
      [e.target.name]:e.target.value
    })
  }

  emitNameEmpty = () => {
    this.userNameInput.focus();
    this.setState({
      name: ''
    });
  }

  emitEmailEmpty = () => {
    this.userNameInput.focus();
    this.setState({
      email: ''
    });
  }



  handleLogout(){
    this.props.logout()
    this.props.history.push('/login')

  }

  render() {
    const FormItem = Form.Item
    const {name,email,gender} = this.state
    const formItemLayout = {
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
    }

    const nameSuffix = name ? <Icon type="close-circle" onClick={this.emitNameEmpty} /> : null
    const emailSuffix = email  ? <Icon type="close-circle" onClick={this.emitEmailEmpty} /> : null
    return (
      <Layout>
        <Header className="header">

          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={['1']}
            style={{ lineHeight: '64px' }}
          >
                  <Menu.Item key="1" onClick={()=>this.props.history.push('/dashboard')}>Home </Menu.Item>
                  <Menu.Item key="logout" onClick={this.handleLogout}>Logout</Menu.Item>

          </Menu>

        </Header>
        <Layout style={{ padding: '0 24px 24px' }}>
          <Breadcrumb style={{ margin: '16px 0' }}>
            <Breadcrumb.Item>Pages</Breadcrumb.Item>
            <Breadcrumb.Item>Edit</Breadcrumb.Item>
          </Breadcrumb>
          <Content style={{ background: '#fff', padding: 24, margin: 0, minHeight: 280 }}>


            <div className='divEdit'>
            <Form  className=''>
              <FormItem    {...formItemLayout} label="Name">
                <Input
                  name='name'
                  placeholder="Enter your username"
                  prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  suffix={nameSuffix}
                  value={name}
                  onChange={this.onChange}
                  ref={node => this.userNameInput = node}
                />
              </FormItem>
              <FormItem  {...formItemLayout} label="Email" >
                <Input
                  name='email'
                  placeholder="Enter your password"
                  type="email"
                  prefix={<Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />}
                  suffix={emailSuffix}
                  value={email}
                  onChange={this.onChange}
                  ref={node => this.userNameInput = node}
                />
              </FormItem>

              <FormItem>




                <Button type="primary"  className="login-form-button" onClick={this.handleLogin}>
                  Confirm
                </Button>
              </FormItem>
            </Form>
          </div>


          </Content>
        </Layout>
      </Layout>
    )
  }
}

export default withRouter(connect (null,{logout})(Edit))
